.footer{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 50px;

}
.footer_logo{
    display: flex;
    align-items: center;
    gap: 20px;

}
.footer_logo p{
    color: #383838;
    font-size: 46px;
    font-weight: 700;
}
.footer-links{
    display: flex;
    list-style: none;
    gap: 50px;
    color: #252525;
    font-size: 20px;

}
.footer-links li{
    cursor: pointer;
}
.footer-social-icon{
    display: flex;
    gap: 20px;

}
.footer-icons-container{
    padding: 10px ;
    padding-bottom: 6px;
    background: #fbfbfb;
    border: 1px solid #ebebeb;

}
.footer copyright{
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 30px;
    width: 100%;
    margin-bottom: 30px;
    color:#1a1a1a;
    font-size: 20px;

}
.footer copyright hr{
    width: 80px;
    border: none;
    border-radius: 10px;
    height: 3px;
    background: white;
}
@media(max-width:800px){
    .footer{
        gap: 20px;

    }
    .footer_logo{
        gap: 10px;
        align-items: end;
    }.footer_logo img{
        width: 40px;
    }.footer_logo p{
        font-size: 25px;
    }.footer-links{
        font-size: 14px;
        gap: 10px;
    }.footer-social-icon{
        width: 22px;
    }.footer-copyright{
        font-size: 13px;
    }
}