.product-display{
    display: flex;
    margin: 0px 170px;
    
}.product-display-left{
    display: flex;
    gap: 17px;

}
.product-image-list{
    display: flex;
    flex-direction: column;
    gap: 16px;

}
.product-image-list img{
    height: 163px;
    
}.productdisplay-main-img{
    width: 586px;
    height: 700px;
}.product-display-right{
    margin: 0px 70px;
    display: flex;
    flex-direction: column;

}.product-display-right h1 {
    color: #3d3d3d;
    font-size: 40px;
    font-weight: 700;

}.productdisplay-right-star{
      display: flex;
      align-items: center;
      margin-top: 13px;
      gap: 5px;
      color: #1c1c1c;
      font-size: 16px;
}.productdisplay-right-prices{
    display: flex;
    margin: 40px 0px;
    gap: 30px;
    font-size: 24px;
    font-weight: 700;
}.productdisplay-right-prices-old{
    color: #818181;
    text-decoration: line-through;
}.productdisplay-right-prices-new{
    color: #ff4141;
}.productdisplay-right-size h1{
      margin-top: 55px;
      color: #656565;
      font-size: 20px;
      font-weight: 600;

}.productdisplay-right-sizes{
    display: flex;
    margin: 30px 0px;
    gap: 20px;
}
.productdisplay-right-sizes div{
    padding: 18px 24px;
    background: #fbfbfb;
    border: 1px solid #ebebeb;
    border-radius: 3px;
    cursor: pointer;

}.product-display-right button{
    padding: 20px 40px;
    width: 200px;
    font-size: 16px;
    font-weight: 600;
    color:white ;
    background: #ff4141;
    margin-bottom: 40px;
    border: none;
    outline: none;
    cursor: pointer;

}.productdisplay-right-category{
    margin-top: 10px;
}
.productdisplay-right-category span{
    font-weight:600 ;
}
@media(max-width:1280px){
    .product-display{
        margin: 0px 60px;
    }
    .product-display-left{
        gap: 10px;
    }
    .product-image-list{
        gap: 10px;
    }.product-image-list img{
        height: 120px;
    }.productdisplay-main-img{
        width: auto;
        height: 510px;

    }.product-display-right{
        margin: 0px 30px;
    }.product-display-right h1{
        font-size: 22px;
    }.productdisplay-right-star{
        gap: 3px;
        font-size: 13px;
    }.productdisplay-right-prices{
        margin: 10px 0px;
        font-size: 18px;
    }.productdisplay-right-description{
        font-size: 13px;
    }.productdisplay-right-size h1{
        margin-top: 20px;
        font-size: 20px;
    }.productdisplay-right-size div{
        padding: 14px 20px;
    }.product-display-right button{
        width: 150px;
        padding: 15px 0px;
        margin-bottom: 20px;
    }.productdisplay-right-category{
        margin-top: 5px ;
        font-size: 14px;
    }
}
@media(max-width:1024px){
    .product-display{
        margin: 0px 30px;
    }
    .product-display-left{
        gap: 5px;
    }
    .product-image-list img{
        height: 80px;
    }.productdisplay-main-img{
    height: 350px;

    }.product-display-right h1{
        font-size: 18px;
    }.productdisplay-right-star img{
        width: 15px;
    }
.productdisplay-right-description{
        font-size: 12px;
    }.productdisplay-right-sizes
    {
        margin-top: 10px 0px;
    }
    .productdisplay-right-size div{
        padding: 10px 16px;
        font-size: 12px;
    }.product-display-right button{
        width: 120px;
        padding: 10px 0px;
        margin-bottom: 10px;
        font-size: 14px;
    }.productdisplay-right-category{
       font-size: 12px;
    }
}
@media(max-width:800px){
    .product-display{
        margin: 0px 10px;
    }
    .product-display-left{
        gap: 0px;
        flex-direction: column-reverse;
        align-items: center;
    }.product-image-list{
        flex-direction: row;
        gap: 6px;
    }
    .product-image-list img{
        height: 70px;
    }.productdisplay-main-img{
    height: 300px;

    }.product-display-right h1{
        font-size: 14px;
    }.productdisplay-right-star img{
        width: 13px;
    }.productdisplay-right-size h1{
        margin-top: 10px;
    }
.productdisplay-right-description{
        font-size: 10px;
    }.productdisplay-right-sizes
    {
        margin-top: 10px ;
        gap: 5px;
    }
    .productdisplay-right-size div{
        padding: 4px 11px;
        
    }.product-display-right button{
        width: 100px;
       font-size: 12px;
    }.productdisplay-right-category{
       font-size: 10px;
    }
}
@media(max-width:500px){
    .product-display{
        flex-direction: column;
    }.product-display-left{
        gap: 10px;
        flex-direction: row;
        margin: auto;
    }.product-image-list{
        flex-direction: column;
        gap: 8px;
    }.product-image-list img{
        height: 75px;

    }.productdisplay-main-img{
        height: 330px;
    }.product-display-right{
        margin: 5px;
    }.product-display-right h1{
        margin-top: 15px;
        font-size: 20px;
        font-weight: 300;
    }.productdisplay-right-star img{
        width: 15px;

    }.productdisplay-right-sizes{
        gap: 10px;
        margin: 20px 0px;
    }.productdisplay-right-sizes div{
        padding: 10px 16px;
        font-size: 14px;
  
}.product-display-right button{
    width: 130px;
    font-size: 15px;
    padding: 12px 0px;
}.productdisplay-right-category{
    font-size:16px;
}
}