.description-box{
    margin: 120px 170px;
}.descriptionbox-navigator{
    display: flex;
}.descriptionbox-navi-box{
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 16px;
    font-weight: 600;
    width: 171px;
    height: 70px;
    border: 1px solid #d0d0d0 ;

}.descriptionbox-navi-box-fade{
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 16px;
    font-weight: 600;
    width: 171px;
    height: 70px;
    border: 1px solid #d0d0d0 ;
    background: #fbfbfb;
    color: #555;
}.descriptionbox-description{
    display: flex;
    flex-direction: column;
    gap: 25px;
    border: 1px solid #d0d0d0;
    padding: 48px;
    padding-bottom: 70px;
}
@media(max-width:1280px){
    .description-box{
        margin: 60px;
    }
}
@media(max-width:800px){
    .description-box{
        margin: 60px 20px;
    }.descriptionbox-description{
        font-size: 14px;
    }
}